@import '../../index.scss';

.toast {
    max-width: 15rem;
    padding: 1rem;
    position: fixed;
    bottom: 2rem;
    right: 3rem;
    color: $light;
    opacity: 0;
    transition: 0.3s;
  
    &.success,
    &.error {
      opacity: 1;
    }
  
    &.success {
      background-color: rgb(28, 199, 28);
    }
  
    &.error {
      background-color: rgb(248, 72, 72);
    }
  }
  