@import "../../index.scss";

.home {
  header {
    height: 100vh;
    display: flex;
    align-items: center;

    h2,
    p {
      width: 70%;
      text-align: left;
    }

    // button{
    //     margin: 2rem 0 0 0;
    // }
  }

  main {
    h2 {
      width: 45%;
      font-family: "Roboto", sans-serif;
    }

    button {
      margin: 2rem 0 0 0;
    }

    section {
      p {
        margin: 1rem 0;
      }

      .slider {
        margin: 5rem 0 -5rem 0;
        padding: 3rem 0;
        background-image: url(./assets/bg.png);

        .slick-track {
          display: flex;
          align-items: center;
        }

        .card {
          padding: 1rem;
          border-left: solid 0.2rem $primary;

          h6 {
            width: max-content;
            border-bottom: 0.15rem solid transparent;
            transition: 0.3s;
          }

          &:hover {
            h6 {
              border-color: $primary;
            }
          }
        }
      }

      &.contactUs {
        height: 100vh;
        display: flex;
        align-items: center;
        position: relative;
        background-image: url(./assets/contactUsBg.jpg);
        background-repeat: no-repeat;
        background-size: cover;

        h2 {
          position: relative;
          color: $dark;
        }

        &::before {
          content: "";
          width: 60%;
          height: 100%;
          display: block;
          position: absolute;
          top: 0;
          left: 0;
          background: linear-gradient(
            90deg,
            rgba(255, 255, 255, 0.7) 40%,
            rgba(255, 255, 255, 0.35) 70%,
            rgba(255, 255, 255, 0) 100%
          );
        }
      }
    }
  }

  @media only screen and(max-width: 900px) {
    header {
      h2,p {
        width: 100%;
      }
    }

    main {
      section {
        &.contactUs {
          height: max-content;
          padding: 5rem 0 15rem 0;

          &::before {
            width: 100%;
            background: rgba(255, 255, 255, 0.7);
          }

          h2 {
            width: 100%;
          }
        }
      }
    }
  }
}
