@import "../../index.scss";

.select {
  position: relative;
  cursor: pointer;

  &::after {
    content: "";
    padding: 3px;
    display: inline-block;
    position: absolute;
    top: 40%;
    right: 0.8rem;
    border: solid $light;
    border-width: 0 3px 3px 0;
    transform: rotate(45deg);
    -webkit-transform: rotate(45deg);
  }

  .list {
    width: 100%;
    max-height: 0;
    position: absolute;
    top: 100%;
    left: 0;
    overflow-y: hidden;
    transition: 0.3s;
    z-index: 1;

    &.open {
      max-height: 12rem;
    }

    li {
      background-color: $dark;

      &:first-of-type {
        border-top: 0.15rem solid $primary;
      }
    }
  }
}
