@import "../../index.scss";

footer {
    margin: -10rem 0 0 0;
    position: relative; 
    background-image: url(./assets/footer.png);
    background-size: cover;

  .top {
    padding: 3rem 0;

    .row{
        margin: 2rem 0 0 0;
    }

    svg {
      height: 1.5rem;
      margin: 0 0.5rem 0 0;
      fill: $primary;
    }
  }

  .bottom {
    padding: 1rem 0;
    background-color: #1d1d1d;
  }

  @media only screen and(max-width: 900px) {
    background-image: url(../home/assets/bg.png);
  }
}
