@import '../../index.scss';

.careers{
    .card{
        margin: 1rem 0;
        padding: 1rem;
        border-bottom: 0.2rem solid $primary;
        transition: 0.3s;

        &:hover{
            box-shadow: inset 0 -1rem 1rem -1rem $primary;
        }
    }

    ul{
        list-style: square;

        li{
            margin: 0.5rem 0 0.5rem 2.5rem;
        }
    }
}