@import "../../index.scss";

.contactUs {
  .container {
    justify-content: space-between;

    & > div > div,
    form {
      padding: 2rem 4rem;
      background-color: #1d1d1d;
    }

    & > div {
      width: 40%;

      & > div {
        margin: 1rem 0;
      }

      svg {
        width: 4rem;
        margin: 0 1rem 0 0;
        fill: $primary;
      }
    }

    form {
      width: calc(60% - 1rem);

      h5 {
        margin: 0 0 3rem 0;
      }

      .row {
        justify-content: space-between;

        input,
        & > ul {
          width: calc(50% - 0.5rem);
          margin: 0.5rem 0;
          background-color: $dark;
        }

        input,
        ul li {
          padding: 0.8rem;

          &.error {
            border: solid 0.15rem red;
          }
        }

        input {
          border: none;
          outline: none;

          &::placeholder {
            color: $light;
          }
        }
      }

      button {
        margin: 1.5rem 0 0 auto;
        display: block;

        span {
          padding: 0.8rem 1.5rem;
        }
      }
    }
  }

  @media only screen and(max-width: 1200px) {
    .container {
      & > div > div,
      form {
        padding: 1.5rem;
      }
    }
  }

  @media only screen and(max-width: 900px) {
    .container {
      flex-wrap: wrap;

      & > div > div{
        display: none;  
      }
      
      form {
        width: 100%;
      }
    }
  }

  @media only screen and(max-width: 700px) {
    .container{
      form{
        .row{
          flex-wrap: wrap;

          input, .select{
            width: 100%;
          }
        }
      }
    }
  }
}
