@import "../../index.scss";

nav {
  width: 100%;
  padding: 1rem 0;
  position: absolute;
  top: 2rem;

  .container {
    justify-content: space-between;
  }

  svg {
    width: 2rem;
    fill: $light;
  }

  .menu,
  .close {
    display: none;
    cursor: pointer;
    transition: 0.3s;

    &:hover{
      fill: $primary;
    }
  }

  .pages {
    position: relative;

    .close {
      position: absolute;
      top: 1rem;
      right: 0.5rem;
    }

    p {
      @include gradient();
      font-weight: 900;
      -webkit-background-clip: text;
      -webkit-text-fill-color: transparent;
    }

    h6 {
      margin: 0 1rem;
      text-align: right;
      @include tabUnderline();
      cursor: pointer;
    }
  }

  .phone {
    align-items: stretch;

    div {
      &:first-of-type {
        @include gradient();

        svg {
          width: 1.5rem;
          margin: 0 0.8rem;
        }
      }
      &:last-of-type {
        margin: 0.25rem 0;
        padding: 0.5rem 1rem;
        background-color: $dark;

        p {
          color: lightgray;
        }
      }
    }
  }

  @media only screen and(max-width: 900px) {
    .phone {
      display: none;
    }
  }

  @media only screen and(max-width: 700px) {
    .menu,
    .close {
      display: block;
    }

    .pages {
      width: 300px;
      height: 100vh;
      padding: 5rem 0;
      display: flex;
      flex-direction: column;
      align-items: flex-end;
      position: fixed;
      top: 0;
      left: -130%;
      background-color: rgba($color: $dark, $alpha: 0.9);
      border-right: solid 0.2rem $primary;
      transition: 0.3s;

      &.open{
        left: 0;
      }

      a {
        margin: 1rem 0;
      }

      h6 {
        font-size: 1.3rem;

        p {
          font-size: 1rem;
        }
      }
    }
  }
}
