@import "../../index.scss";

button {
  padding: 0.15rem;
  position: relative;
  color: $light;
  font-weight: 900;
  text-transform: uppercase;
  @include gradient();
  border: none;
  outline: none;
  cursor: pointer;
  transition: 0.3s;

  &.primary {
    span {
      color: $dark;
      @include gradient();
    }
  }

  &:hover {
    box-shadow: 0 0 1rem $primary;

    span {
      box-shadow: inset 0 0 0.5rem $primary;
    }
  }

  span {
    padding: 1rem 2.5rem;
    display: block;
    background-color: $dark;
    transition: 0.3s;
  }
}
