@import url("https://fonts.googleapis.com/css2?family=Roboto:wght@300;500;700&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Bebas+Neue&display=swap");

$dark: #161515;
$light: #ffffff;
$primary: #f4a21a;
$secondary: #ffdd00;

@mixin gradient {
  background: linear-gradient(90deg, rgba(244, 162, 26, 1) 0%, rgba(255, 221, 0, 1) 100%);
}

@mixin tabUnderline {
  position: relative;

  &::after {
    content: "";
    width: 0;
    height: 2px;
    position: absolute;
    top: 100%;
    left: 0;
    @include gradient();
    transition: 0.3s;
  }

  &:hover {
    &::after {
      width: 100%;
    }
  }
}

*,
*::before,
*::after {
  color: $light;
  box-sizing: border-box;
  scroll-behavior: smooth;
  font-family: "Roboto", sans-serif;
}

a,
a:visited,
a:active {
  color: inherit;
  text-decoration: none;
}

body,
h1,
h2,
h3,
h4,
h5,
h6,
p {
  margin: 0;
}

h1,
h2,
h3,
h4,
h5,
h6,
button span {
  text-transform: uppercase;
  font-family: "Bebas Neue", cursive;
  letter-spacing: 1px;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

h1 {
  font-size: 5rem;
  @media only screen and(max-width: 1200px) {
    font-size: 3.5rem;
  }
  @media only screen and(max-width: 768px) {
    font-size: 2rem;
  }
}
h2 {
  font-size: 3.5rem;
  @media only screen and(max-width: 768px) {
    font-size: 1.7rem;
  }
}
h3 {
  font-size: 2.5rem;
  @media only screen and(max-width: 768px) {
    font-size: 1.5rem;
  }
}
h4 {
  font-size: 2rem;
  @media only screen and(max-width: 768px) {
    font-size: 1.5rem;
  }
}
h5 {
  font-size: 1.5rem;
  @media only screen and(max-width: 768px) {
    font-size: 1.2rem;
  }
}
h6 {
  font-size: 1.2rem;
  @media only screen and(max-width: 768px) {
    font-size: 1rem;
  }
}
p,
a,
td,
li,
label,
input,
button {
  font-size: 1rem;
  line-height: 1.5rem;
  @media only screen and(max-width: 768px) {
    font-size: 0.8rem;
  }
}

body {
  width: 100%;
  background-color: $dark;
  overflow-x: hidden;
}

.container {
  width: 1100px;
  margin: auto;
  @media only screen and(max-width: 1200px) {
    width: 95%;
  }
}

//******************flex******************
.row {
  display: flex;
  align-items: center;
}

.column {
  display: flex;
  flex-direction: column;
}

//******************custom scrollBar******************
::-webkit-scrollbar {
  width: 0.8rem;
  height: 0.8rem;
  background-color: $light;

  /* ползунок скроллбара */
  &-thumb {
    border-radius: 0.2rem;
  }
}

/* Стрелки */
::-webkit-scrollbar-button:vertical:start:decrement {
  background: linear-gradient(120deg, $light 40%, rgba(0, 0, 0, 0) 41%),
    linear-gradient(240deg, $light 40%, rgba(0, 0, 0, 0) 41%), linear-gradient(0deg, $light 30%, rgba(0, 0, 0, 0) 31%);
}

::-webkit-scrollbar-button:vertical:end:increment {
  background: linear-gradient(300deg, $light 40%, rgba(0, 0, 0, 0) 41%),
    linear-gradient(60deg, $light 40%, rgba(0, 0, 0, 0) 41%), linear-gradient(180deg, $light 30%, rgba(0, 0, 0, 0) 31%);
}

::-webkit-scrollbar-button:horizontal:start:decrement {
  background: linear-gradient(30deg, $light 40%, rgba(0, 0, 0, 0) 41%),
    linear-gradient(150deg, $light 40%, rgba(0, 0, 0, 0) 41%), linear-gradient(270deg, $light 30%, rgba(0, 0, 0, 0) 31%);
}

::-webkit-scrollbar-button:horizontal:end:increment {
  background: linear-gradient(210deg, $light 40%, rgba(0, 0, 0, 0) 41%),
    linear-gradient(330deg, $light 40%, rgba(0, 0, 0, 0) 41%), linear-gradient(90deg, $light 30%, rgba(0, 0, 0, 0) 31%);
}

::-webkit-scrollbar-thumb,
::-webkit-scrollbar-button:vertical:end:increment,
::-webkit-scrollbar-button:vertical:start:decrement,
::-webkit-scrollbar-button:horizontal:start:decrement,
::-webkit-scrollbar-button:horizontal:end:increment {
  background-color: grey;

  &:hover {
    background-color: $dark;
  }
}

//******************hide browser input buttons******************
//******number******
input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

input[type="number"] {
  -moz-appearance: textfield;
}

//******************custom checkbox******************
input[type="checkbox"] {
  padding: 0;
  height: initial;
  width: initial;
  margin-bottom: 0;
  display: none;
  cursor: pointer;

  & + label {
    position: relative;
    cursor: pointer;

    &::before {
      content: "";
      margin: 0.5rem 0;
      -webkit-appearance: none;
      background-color: transparent;
      border: 2px solid #0079bf;
      box-shadow: 0 1px 2px rgba(0, 0, 0, 0.05), inset 0px -15px 10px -12px rgba(0, 0, 0, 0.05);
      padding: 10px;
      display: inline-block;
      position: relative;
      vertical-align: middle;
      cursor: pointer;
      margin-right: 5px;
    }
  }
}

input:checked + label:after {
  content: "";
  display: block;
  position: absolute;
  top: 2px;
  left: 9px;
  width: 6px;
  height: 14px;
  border: solid #0079bf;
  border-width: 0 2px 2px 0;
  transform: rotate(45deg);
}

header {
  height: 60vh;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #0b0b0b;
  background-image: url(./components/assets/bg.png);
  background-size: 70vw;
  background-position: 100% 0;
  background-repeat: no-repeat;

  h2 {
    text-align: center;
  }

  @media only screen and(max-width: 1200px) {
    height: max-content !important;
    padding: 10rem 0;
    background-size: 100vw;
  }
}

main {
  background-image: -webkit-repeating-linear-gradient(315deg, #232323, #232323 5%, transparent 5%, transparent 50%),
    -webkit-repeating-linear-gradient(225deg, #232323, #232323 5%, transparent 5%, transparent 50%);
  background-image: repeating-linear-gradient(135deg, #232323, #232323 5%, transparent 5%, transparent 50%),
    repeating-linear-gradient(-135deg, #232323, #232323 5%, transparent 5%, transparent 50%);
  background-size: 6px 6px;

  section {
    padding: 5rem 0;

    &:last-of-type {
      padding-bottom: 15rem;
    }
  }
}

.logo {
  img {
    width: 3.5rem;
  }

  h3 {
    margin: 0 0 0 1rem;
  }
}

.slick-arrow{
  display: none !important;
}